* {
  margin: 0;
  padding: 0;
}

nav {
  background: rgb(169, 169, 169);
  color: white;
}

main {
  width: 84rem;
  background: rgb(169, 169, 169);
  position: relative;
  left: -11px;
}
#head {
  font-weight: bolder;
  font-style: italic;
}
.png {
  margin: 20px;
  width: 104px;
  height: 104px;
}
.card-contianer {
  width: 98vw;
}
.col {
  /* margin-left: 32px; */
  width: 29vw;
}
#col1 {
  margin-left:7rem;
}
.row {
  width: 84rem;
  display: flex;
  flex-direction: row;
}
.dark {
  background: black;
  color: white;
}

.day-mode {
  width: 37px;
  height: 35px;
  background: url("../src/images/dark-mode.png") no-repeat center center/cover;
  border: 2px solid #6c757d;
}
.night-mode {
  width: 37px;
  height: 35px;
  background: url("../src/images/sun.png") no-repeat center center/cover;
  border: 2px solid #212529;
}
.header {
  border-radius: 6px;
  border: 1px solid white;
  height: 64px;
}
.cbody {
  border: 1px solid rgb(255, 255, 255);
  border-radius: 6px;
  padding: 10px;
  height: 25rem;
}
#logo {
  width: 90px;
  height: 90px;
}
a {
  color: white;
}
.text {
  font-size: 30px;
  color: white;
  text-decoration: none;
  font-weight: bolder;
}
.text2 {
  margin: 12px;
  color: white;
  text-decoration: none;
  position: relative;
  right: -3px;
  bottom: -5px;
}
.tc {
  padding: 16px 36px;
  color: white;
  width: 84rem;
box-sizing: border-box;
}
.change {
  color: white;
}
.color {
  color: black;
}
th {
  width: 10%;
  box-sizing: border-box;
}



/* responsiveness establishment */
@media screen and (max-width: 1000px) {
  /* navbar specific adjustment */
  html {
    height: 844px;
  }
  .navbar-toggler {
    width: 51px;
    height: 47px;
    font-size: 46px;
  }
  .text {
    font-size: 22px;
  }
  #logo {
    width: 107px;
    height: 99px;
  }
  .icon {
    bottom: 9px;
    font-size: 37px;
    position: relative;
    right: 4px;
  }
  .navbar-nav {
    flex-direction: row;
    /* align-items: center; */
    font-size: 22px;
    justify-content: center;
  }
  /* mode button specific adjustment */

  .night-mode {
    width: 49px;
    height: 54px;
  }
  .day-mode {
    width: 49px;
    height: 54px;
  }
  /* search specific adjustment */
  .d-flex {
    justify-content: center;
  }
  .form-control {
    font-size: 1.5rem;
    width: 62%;
    border-radius: 30px;
  }

  /* weather of text adjestment */
  .mb-4 {
    font-size: 24px;
  }

  /* card specific adjustment */
  .card {
    width: 21rem;
    height: 436px;
  }
  main {
    width: 24rem;
  }
  .row {
    width: 99%;
    margin-left: 3px;
    margin-right: 0px;
  }
  .card-contianer {
    width: 100%;
  }
  .rounder-3 {
    border-radius: 28px;
  }
  .header {
    border-radius: 22px;
  }
  .cbody {
    border-radius: 22px;
    height: 370px;
  }
  .w-100 {
    top: -16px;
    position: relative;
    width: 100% !important;
    /* height: 26px; */
    padding: 0px;
  }
  .col {
    margin: 2px 18px;
    width: 12rem;
  }
  #col1 {
    margin-left: 19px;
  }
td{
  padding: 20px;
}
}
